<style lang=less>
  @import '../../../assets/index.less';
  @import './bindTel.less';
</style>
<template>
  <div class="base-info bindTel">
    <!-- 绑定邮箱 -->
    <p class="title">
      <span></span>{{$t('center.bindEmail[0]')}}
    </p>
    <el-form  class="bindTelForm" :model="bindEmail">
      <el-form-item :label="$t('center.bindEmail[1]')">
        <el-input v-model="bindEmail.email" :placeholder="$t('center.bindEmail[2]')"></el-input>
      </el-form-item>
      <div style="overflow: hidden;">
        <el-col :span="18">
        <el-form-item :label="$t('center.bindEmail[3]')">
          <el-input v-model="bindEmail.code" :placeholder="$t('center.bindEmail[4]')"></el-input>
        </el-form-item>
        </el-col>
        <el-col :span="6" class="codeBtn">
          <el-button type="primary" style="width: 112px;" :loading="getCodeLoading" :disabled="!ableGetCode" @click="getCodeRequest">{{getCodeText}}</el-button>
        </el-col>
      </div>
      <el-form-item :label="$t('center.bindEmail[5]')">
        <el-input v-model="bindEmail.moneyPwd" show-password :placeholder="$t('center.bindEmail[6]')"></el-input>
      </el-form-item>
      <el-button class="submit" type="primary" :loading="submitLoading" @click="submit">{{$t('center.bindEmail[7]')}}</el-button>
    </el-form>
  </div>
</template>
<script>
import {bindEmail,sendMail} from "@/api/user.js"
import {sendMsg} from '@/api/public.js'
export default {
  data () {
    return {
      submitLoading:false,
      bindEmail:{//表单中数据
        email:'',
        code:'',
        moneyPwd:''
      },
      countDownNumber:60,//倒计时秒数
      countDownTimer:null,//定时器
      getCodeText: this.$t('center.bindEmail[8]'),//验证码处显示的内容
      ableGetCode:true,//验证码按钮能否点击
      sessionData:{},
      tel:'',
      getCodeLoading:false
    }
  },
  created () {
    this.sessionData = JSON.parse(sessionStorage.getItem('userInfo'))
    this.tel = this.sessionData.mobile
  },
  watch: {
  },
  methods: {
    submit(){//提交
      if(!this.$public.email(this.bindEmail.email)){
        this.$public.msg(this.$t('center.bindEmail[9]'),'warning',this)
        return
      }

      if(!this.$public.code(this.bindEmail.code)){
        this.$public.msg(this.$t('center.bindEmail[10]'),'warning',this)
        return
      }

      if(!this.$public.pwd(this.bindEmail.moneyPwd)){
        this.$public.msg(this.$t('center.bindEmail[11]'),'warning',this)
        return
      }
      this.$public.googleVerify().then((r)=> {
        if(r){//验证通过
            this.submitRequest()
        } else{
            this.$public.msg(this.$t('center.bindEmail[12]'),'error',this)
        }
      }).catch((r)=>{
        console.log('取消')
      })

    },
    submitRequest(){
      this.submitLoading = true
      let _data = {
        account: JSON.parse(sessionStorage.getItem('account')),
        email: this.bindEmail.email,
        mobile: this.tel,
        code: this.bindEmail.code,
        tpwd: this.$public.Cryptographiccapital(this.bindEmail.moneyPwd)
      }
      bindEmail(_data).then((res)=>{
        this.submitLoading = false
        if(res.data.status == 200){
          this.$public.msg(this.$t('center.bindEmail[13]'),'success',this)
          this.$router.back()
        } else{
          this.$public.msg(res.data.msg,'error',this)
          this.bindEmail.email = ''
          this.bindEmail.moneyPwd = ''
          this.bindEmail.code = ''
        }
      })
    },
    countDown(){//验证码倒计时
      this.ableGetCode = false
      let countDownCopy = this.countDownNumber
      this.countDownTimer = setInterval(()=>{
        if(this.countDownTimer && countDownCopy<=0){
          clearInterval(this.countDownTimer)
          this.getCodeText = this.$t('center.bindEmail[14]')
          this.ableGetCode = true
          return
        }
        this.getCodeText = countDownCopy
        countDownCopy--;
      },1000)
    },
    getCodeRequest(){//发送获取验证码请求接口
      if(!this.$public.email(this.bindEmail.email)){
        this.$public.msg(this.$t('center.bindEmail[15]'),'warning',this)
        return
      }
      this.getCodeLoading = true
      this.ableGetCode = false
      let _data = {
        email:this.bindEmail.email
      }
      sendMail(_data).then((res)=>{
        this.getCodeLoading = false
        if(res.data.status == 200){
          this.$public.msg(this.$t('center.bindEmail[16]'),'success',this)
          this.countDown()
        } else{
          this.$public.msg(res.data.msg,'error',this)
          this.ableGetCode = true
        }
      },
      ()=>{
        this.$public.msg(this.$t('center.bindEmail[17]'),'error',this)
        this.getCodeLoading = false
        this.ableGetCode = true
      })
    }
  }
}
</script>



